/* Canvas size */
#app {
  height: 100%;
  width: 100%;
}
.container-fluid {
  padding-left: 0px;
  padding-right: 0px;
}
.row {
  margin-left: 0px;
  margin-right: 0px;
}
.size {
  height: 100%;
  width: 100%;
}
.margin {
  margin: 0px;
}
.padding {
  padding-left: 0px;
  padding-right: 0px;
}

/* Paper */
.paper {
  position: fixed;
  z-index: 10;
  width: 10vw;
}

/* Lottie + textura */
.scene {
  width: 100%;
  height: 100%;
}
.lottie {
  z-index: -100;
  width: 100%;
  margin: 0px;
  padding: 0px;
  position: absolute;
}
.textura {
  width: 100%;
  z-index: 999;
  position: absolute;
}

/* Buttons */
.buttoncontainer {
  height: 100%;
  width: 100%;
  z-index: 99999;
  position: absolute;
}
.yellowbutton {
  background: url(button.svg);
  height: 100px;
  width: 100px;
  margin-top: 155%;
  margin-left: 39%;
}

#myCanvas {
  /* position:fixed; */
  height: 500px;
  width: 500px;
  z-index: 10;
  background-color: transparent !important;
}

canvas[resize] {
  width: 100%;
  height: 100%;
}
